import { useNavigate, useParams } from 'react-router-dom';
import { useState, useEffect } from 'react';

import SliderApp from './SliderApp';
import { Comments } from './Comments';
import { StarDevice } from './conmopents/Start/StarDevice';
import { SaleProduct } from '../Shop/Goods/components/ItemProduct/ItemProduct';
import { HeartBtn, LoadingWin, MetaData, ProductBtn, btnHOC } from 'components';
import { useAppDispatch, useAppSelector } from '../../store';
import { changeModal } from '../../store/modalSlice/modalSlice';
import { setRating } from '../../store/goodsSlices/ratingSlice/ratingSlice';
import { getProduct } from '../../store/goodsSlices/productSlice/productSlice';

import './style/OneProduct.scss';

const OneProduct: React.FC = () => {
  const dispatch = useAppDispatch();
  const { product, isLoading } = useAppSelector((state) => state.product);

  const [elem, setElem] = useState('DETAILS');
  const path = useParams().id;
  const router = useNavigate();
  const Btn = btnHOC(ProductBtn);
  async function fetching() {
    if (path) {
      dispatch(getProduct({ id: +path }));
    }
    if ('brand' in product) {
      document.title = `${product?.brand.name} | ${product?.name}`;
    }
  }

  useEffect(() => {
    if (!isLoading) dispatch(setRating({ rating: product.ratings }));
  }, [isLoading]);

  function open(com, sub) {
    dispatch(changeModal({ isActive: true, component: com, subCom: sub }));
  }

  useEffect(() => {
    fetching();
  }, []);

  return (
    <MetaData
      title={`FightGym product - ${product?.type?.name}`}
      disc={`FightGym product - ${product?.type?.name} - ${product?.brand?.name}`}
    >
      <div className='oneproduct indent_last'>
        <LoadingWin isLoading={isLoading}>
          <div className='container'>
            <div className='indentstart'></div>
            <div className='oneproduct__wrap'>
              <div className='oneproduct__row'>
                <div className='oneproduct__pathblock'>
                  <div className='oneproduct__path'>
                    <p
                      onClick={() => {
                        router(`/`);
                      }}
                      className='mini'
                    >
                      Home
                    </p>
                  </div>
                  <div className='oneproduct__path'>
                    <p
                      onClick={() => {
                        router(`/shop`);
                      }}
                      className='mini'
                    >
                      Shop
                    </p>
                  </div>
                  <div translate='no' className='oneproduct__path'>
                    <p
                      onClick={() => {
                        router(`/shop?type=${product?.typeId}`);
                      }}
                      translate='no'
                      className='mini'
                    >
                      {product?.type?.name}
                    </p>
                  </div>
                  <div translate='no' className='oneproduct__path'>
                    <p
                      onClick={() => {
                        router(
                          `/shop?type=${product?.typeId}&brand${product?.brandId}=${product?.brandId}`
                        );
                      }}
                      translate='no'
                      className='mini'
                    >
                      {product?.brand?.name}
                    </p>
                  </div>
                  <div translate='no' className='oneproduct__path'>
                    <p translate='no' className='mini'>
                      {product?.name}
                    </p>
                  </div>
                </div>

                <div className='oneproduct__item'>
                  <SliderApp product={product} />
                  <div className='oneproduct__twoblock'>
                    <div className='oneproduct__title'>
                      <h3 translate='no'>{product?.name}&nbsp;&nbsp;</h3>
                    </div>

                    <div className='oneproduct__brand'>
                      <p className='mini'>{product?.brand?.name}</p>
                    </div>
                    <div className='oneproduct__star'>
                      <StarDevice
                        setElem={setElem}
                        text={'Be the first to review this product'}
                      />
                    </div>

                    <div
                      className={
                        product?.sale > 0
                          ? 'oneproduct__price add'
                          : 'oneproduct__price'
                      }
                    >
                      {!!product && <SaleProduct product={product} />}
                    </div>
                    <div className='oneproduct__add'>
                      <div className='oneproduct__btn'>
                        <Btn
                          product={product}
                          model={open}
                          show={true}
                          item='oneitem'
                        />
                      </div>

                      {product && (
                        <HeartBtn id={product?.id} model={open} one={true} />
                      )}
                    </div>
                    <div className='oneproduct__about  about'>
                      <ul className='about__text'>
                        <li>
                          <p className='mini'>- Lorem ipsum dolor sit amet.</p>
                        </li>
                        <li>
                          <p className='mini'>
                            - Lorem ipsum dolor sit amet consectetur
                            adipisicing.
                          </p>
                        </li>
                        <li>
                          <p className='mini'>
                            - Offers a snug, secure and supportive fit.
                          </p>
                        </li>
                      </ul>
                    </div>
                  </div>
                </div>
              </div>
            </div>

            <Comments
              elem={elem}
              setElem={setElem}
              model={open}
              product={product}
            />
          </div>
        </LoadingWin>
      </div>
    </MetaData>
  );
};

export default OneProduct;
